import React from "react";

function Contact(props) {
  return (
    <>
      <li class="contact">
        <div
          class="wrap"
          onClick={() => {
            props.chatSelect(props.data);
          }}
        >
          <span class={props.data.status}></span>
          <img src={props.data.img} alt="" />
          <div class="meta">
            <p class="name">{props.data.name}</p>
            <p class="preview">
              {props.data.msgs.map((item)=>{
                console.log(item)
              })}
           </p>
          </div>
        </div>
      </li>
    </>
  );
}
export default Contact;
